@charset "utf-8";
*{margin:0px;padding:0px;}
ul, li, ol{list-style:none;}
body{
  font-family: "맑은 고딕",arial, sans-serif;
  font-size:12px;
  color:#333;
}
a{text-decoration:none;color:#333;}

header{
  width:100%;height:156px;
  box-shadow:0px 0px 3px #333;
}
.header_inner{
  width:1050px;height:156px;
  margin:0px auto;
  /* background:#c9c9a4; */
}
main, .product{
  width:1050px; padding:30px 0px;
  height:600px; margin:50px auto;
  /* background:rgb(172, 241, 172); */
}
footer{
  width:100%;
  padding-top:70px;
  border-top:1px solid #ccc;
  /* background:#f5f5dc; */
}
footer .copyright{
  text-align: center;
}
.f_inner{
  width:1050px;
  margin:0px auto;
  height: 350px;
} 
footer address{font-style: normal;  height: 300px;}
.f_left{
  float: left;
  width: 49%;
}
.f_right{
  float: right;
  width: 49%;
}
.f_left .m_num{
  font-weight: bold;
  transform: translateY(20px);
}
.f_left p{
  font-size: 14px;
}
.f_left .t_num{
  font-size: 1.4rem;
}
.b01,.b02,.b03{
  display: block;
  padding-top: 15px;
}
.b01 a,.b02 a,.b03 a{display: inline-block;float: left; border: 1px solid black; padding: 10px; width: 130px; text-align: center;background-color: #fff;margin-right: 10px; border-radius: 3px;}

.b123{
  padding-top: 50px;
}
.f_right ul{
  display: flex;
  margin-top: 20px;
  justify-content: space-around;
}
.f_right ul li{
  font-weight: bold;
  font-size: 14px;

}
.f_right p{
  padding-top: 50px;
}

.header_inner{
  position: relative;
}
.top_menu{
  position: absolute;
  right: 0;
}
.top_menu ul{
  display: flex;
  padding: 10px;
}
.top_menu ul li{
  padding-right: 7px;
}
.top_menu ul li:first-child{color: purple;}
.mid_menu{
  position: relative;
}
.mid_menu h1{
  position: absolute;
  left: 10px;
  top: 50px;
  color: purple;
}
.mid_menu .mkt_btt_menu{
  position: absolute;
  top: 60px;
  left: 100px;
}
.mkt_btt_menu ul{
  display: flex;
}
.mkt_btt_menu ul li{
  font-weight: bold;
  font-size: 1rem;
  padding-right: 10px;
  color: rgb(130, 130, 130);
}
.mkt_btt_menu ul li:first-child{
  color: purple;
}
.search{
  position: absolute;
  left: 33%;
  top: 50px;
}
.search input{
  padding: 13px;
  width: 350px;
  border-radius: 7px;
  border: 1px solid purple;
}
.search #h_search:focus{border: 1px solid purple}
.bottom_menu{
  position: absolute;
  bottom: 10px;
  width: 1050px;
  margin: 0px;
}
.bottom_menu .category{
  display: inline-block;
  font-size: 1.1rem;
  font-weight: bold;
  float: left;
  padding-left: 20px;
}
.bottom_menu .gnb{width: 50%;margin: 0px auto;}
.bottom_menu .gnb ul{display: flex; justify-content: space-around;}
.bottom_menu .gnb ul li{
  font-size: 1rem;
  font-weight: bold;
  float:left;
}


/* top버튼 */
#to-top{
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 10;
  width: 50px;
  height: 50px;
  background-color: #fff;
  color: #000;
  border: 2px solid #000;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}