@charset "utf-8";
ol,li,ul{list-style:none;}
*{padding: 0px; margin: 0;}
body{
  color: #333;
}

h3{text-align:center; padding: 70px 0px 40px 0px; font-size: 1.6rem;}
/* product list서식  */
.product_list{
  width:1050px;
  display:flex;
  justify-content: space-evenly;
  margin: 0px auto;
  padding-bottom: 70px;
}
.product_list > li{
  width:249px;height:434px;
  /* border:1px solid #f00; */
  
}
.product_list .title{
  font-size: 14px;
  height: 30px;
  padding-top: 5px;
}
.product_list > li img{
  height:320px;
}
.product_list .sale{
  font-weight: bold;
  color: red;
  float: left;
  margin-top: 10px;
  font-size: 1rem;
}
.product_list .price{
  font-size: 1rem;
  padding-top: 10px;
  font-weight: bold;
  transform: translateX(-50px);
}
.product_list .icon{
  float: right;
  margin-right: 10px;
  transform: translateY(20px);
  font-size: 1.2rem;
  color: #fff;
  padding: 3px 7px;
  border-radius: 50%;
  background-color: purple;
}
.product_list > li img:hover{
  filter: grayscale(1);
}