*{margin:0px;padding:0px;}
ul,ol,li{list-style:none;}
.slide-wrapper {
    position: relative;
    width: 1900px;
    height: 370px;
    overflow: hidden;
}
.slide{
    display:flex;  width:7720px;
    position:absolute;  left:0px;top:0px;
    transition:0.3s;
}
.slide > li{
    width:1900px;height:370px;  background:#ff0;
}
.slide > li:not(:last-child){margin-right:30px;}
.banner{
    width: 1050px;
    margin: 0px auto;
    margin-top: 50px;
}
.banner img{
    width: 100%;
}

.btn_group > input[type=button]{
    position:absolute;top:47%;
}
.btn_group .prev{left:0px;}
.btn_group .next{right:0px;}