.LoginForm{
    text-align: center;
    padding: 100px 0px;
}
input{
    margin-top: 5px;
    padding: 10px 0px;
    width: 250px;
    border: 1px solid #333;
}
#search{
    font-size: 12px;
    display: block;
    width: 200px;
    margin: 0px auto;
    padding-top: 10px;
}
button{
    border: none;
    width: 250px;
    padding: 10px 0px;
    background-color: rgb(88, 124, 222);
    color: #fff;
    border: 1px solid rgb(88, 124, 222);
    transform: translate(0px,30px);
    cursor: pointer;
}